import {
  Route,
  Routes,
  Navigate,
  BrowserRouter as Router,
} from "react-router-dom";
import "src/styles/globals.scss";
import {
  METRO,
  metroList,
  REGIONS,
  regionsList,
  SetLocal,
  token,
} from "./server/Host";
import {
  Home,
  Login,
  Advert,
  Active,
  Waiting,
  NotFound,
  Inactive,
  Rejected,
} from "./pages";
import { GetMetroConfig, GetRegionsConfig } from "./server/config/Urls";
import { CatchError } from "./utils";
import { useEffect } from "react";

function App() {
  // Get regions at first
  const GetRegions = async () => {
    if (regionsList.length < 1) {
      try {
        const { data } = await GetRegionsConfig();
        SetLocal(REGIONS, JSON.stringify(data));
      } catch (error) {
        CatchError(error);
      }
    }
  };

  // Get Metro at first
  const GetMetro = async () => {
    if (metroList.length < 1) {
      try {
        const { data } = await GetMetroConfig();
        SetLocal(METRO, JSON.stringify(data));
      } catch (error) {
        CatchError(error);
      }
    }
  };

  const CheckDefaults = async () => {
    if (metroList.length < 1 && regionsList.length < 1) {
      window.location.reload();
    }
  };

  useEffect(() => {
    GetMetro();
    GetRegions();
    setTimeout(() => CheckDefaults(), 100);
  }, []);

  return (
    <>
      <Router>
        {/* All pages rendered here */}
        <Routes>
          <Route index element={token ? <Navigate to="/home" /> : <Login />} />
          <Route path="/home" element={token ? <Home /> : <Navigate to="/" />}>
            <Route path="waiting">
              <Route index element={<Waiting />} />
              <Route path=":deep_link" element={<Advert />} />
            </Route>
            <Route path="active">
              <Route index element={<Active />} />
              <Route path=":deep_link" element={<Advert />} />
            </Route>
            <Route path="inactive">
              <Route index element={<Inactive />} />
              <Route path=":deep_link" element={<Advert />} />
            </Route>
            <Route path="rejected">
              <Route index element={<Rejected />} />
              <Route path=":deep_link" element={<Advert />} />
            </Route>
          </Route>
          <Route path="/*" element={<NotFound />} />
        </Routes>
      </Router>
    </>
  );
}

export default App;
