import React, { useState } from "react";
import "src/styles/login.scss";
import { Link } from "react-router-dom";
import { Button, Form, Input } from "antd";
import { MaskedInput } from "antd-mask-input";
import { ACCESS, SetLocal } from "src/server/Host";
import { LoginConfig } from "src/server/config/Urls";
import { CatchError, PrettyPhone } from "src/utils/index";

function Login() {
  const [loading, setLoading] = useState(false);
  const onFinish = async ({ phone, password }: any) => {
    setLoading(true);

    try {
      const { data } = await LoginConfig({
        phone_number: PrettyPhone(phone),
        password,
      });

      SetLocal(ACCESS, data.access);
      window.location.href = "/home/waiting";
    } catch (error) {
      CatchError(error);
    }

    setLoading(false);
  };

  return (
    <div className="login_section">
      <div className="login_left">
        <div className="login_left_wrap">
          <Link to={"/"} className="logo">
            <img src={require("src/assets/images/logo.png")} alt="logo" />
            <span>Ijara.edu.uz</span>
          </Link>

          <div className="login_left_content">
            <p>
              Texnikumlarni muvaffaqiyatli tamomlagan bituruvchilarni OTMlarga
              suhbat asosida oʻqishga qabul qilish tizimi.
            </p>
          </div>
        </div>
      </div>
      <div className="login">
        <div className="login__form">
          <h2>Tizimga kirish</h2>
          <div>
            <Form onFinish={onFinish} autoComplete="off" layout="vertical">
              <Form.Item
                label="Telefon raqamingiz"
                name="phone"
                rules={[
                  {
                    required: true,
                    message: "Telefon raqamni kiriting",
                  },
                  {
                    pattern: /^\(\d{2}\) \d{3} \d{2} \d{2}$/,
                    message: "Iltimos telefon raqamni to'liq kiriting",
                  },
                ]}
              >
                <MaskedInput
                  prefix="+998"
                  mask={"(00) 000 00 00"}
                  size="large"
                />
              </Form.Item>

              <Form.Item
                label="Password"
                name="password"
                rules={[
                  { required: true, message: "Please input your password!" },
                ]}
              >
                <Input.Password size="large" />
              </Form.Item>

              <Form.Item>
                <Button
                  size="large"
                  type="primary"
                  htmlType="submit"
                  loading={loading}
                >
                  Kirish
                </Button>
              </Form.Item>
            </Form>
          </div>
          <p>
            O‘zbekiston Respublikasi qonunchiligi asosida, shaxsingiz
            to‘g’risidagi ma’lumotlarni id.egov.uz tizimidan olinganini ma’lum
            qilamiz
          </p>
        </div>
      </div>
    </div>
  );
}

export default Login;
