import React, { useState, useEffect } from "react";
import { Layout, Menu, Modal } from "antd";
import {
  FileSyncOutlined,
  MenuFoldOutlined,
  FileExcelOutlined,
  MenuUnfoldOutlined,
  FileSearchOutlined,
  FileProtectOutlined,
} from "@ant-design/icons";
import { ACCESS, REFRESH, ROLE } from "src/server/Host";
import { Link, Outlet, useLocation, useNavigate } from "react-router-dom";

const App = () => {
  const url = useLocation();
  const navigate = useNavigate();
  const { Header, Sider, Content } = Layout;
  const [collapsed, setCollapsed] = useState(false);
  const [data, SetData] = useState<any>([]);
  const SidebarMenu = [
    {
      key: "waiting",
      icon: <FileSearchOutlined />,
      label: "Yangi e'lonlar",
      onClick: () => navigate("/home/waiting"),
    },
    {
      key: "active",
      icon: <FileProtectOutlined />,
      label: "Tasdiqlangan e'lonlar",
      onClick: () => navigate("/home/active"),
    },
    {
      key: "inactive",
      icon: <FileSyncOutlined />,
      label: "Nofaol e'lonlar",
      onClick: () => navigate("/home/inactive"),
    },
    {
      key: "rejected",
      icon: <FileExcelOutlined />,
      label: "Rad etilgan e'lonlar",
      onClick: () => navigate("/home/rejected"),
    },
  ];

  const redirect = () => {
    if (url.pathname === "/home" || url.pathname === "/home/") {
      window.location.href = "/home/waiting";
    }
  };
  const Logout = async () => {
    Modal.confirm({
      title: "Tizimdan chiqmoqchimisz ?",
      okText: "Chiqish",
      cancelText: "Bekor qilish",
      onOk() {
        localStorage.removeItem(ACCESS);
        localStorage.removeItem(REFRESH);
        localStorage.removeItem(ROLE);
        window.location.href = "/";
      },
    });
  };

  useEffect(() => {
    redirect();
  }, []);
  return (
    <Layout className="home">
      <Sider
        width={256}
        trigger={null}
        collapsible
        collapsed={collapsed}
        className="home__sidebar"
      >
        <Link to={"/"} className="logo">
          <img src={require("../../assets/images/logo.png")} alt="logo" />
          {!collapsed && <span>Ijara.edu.uz</span>}
        </Link>
        <Menu
          theme="dark"
          mode="inline"
          items={SidebarMenu}
          defaultSelectedKeys={[url.pathname.split("/")[2]]}
        />
      </Sider>
      <Layout className="home__body">
        <Header className="home__header">
          {/* Collapse button */}
          {collapsed ? (
            <MenuUnfoldOutlined
              height={24}
              className="trigger"
              onClick={() => setCollapsed(!collapsed)}
            />
          ) : (
            <MenuFoldOutlined
              className="trigger"
              onClick={() => setCollapsed(!collapsed)}
            />
          )}

          <h2>{data?.university_name}</h2>
          <h4 onClick={Logout} style={{ cursor: "pointer" }}>
            Chiqish
          </h4>
        </Header>
        <Content className="home__content">
          <Outlet />
        </Content>
      </Layout>
    </Layout>
  );
};

export default App;
