import AOS from "aos";
import Card from "src/components/home/card";
import { CatchError } from "src/utils/index";
import React, { useEffect, useState } from "react";
import { GetAdvertsListConfig } from "src/server/config/Urls";
import { useSearchParams } from "react-router-dom";
import { Pagination } from "antd";

function Rejected() {
  const [searchParams, setSearchParams] = useSearchParams();
  const [total, setTotal] = useState(0);
  const currentPage = searchParams.get("page");
  const [current, setCurrent] = useState(currentPage ? currentPage : 1);
  const [adverts, setAdverts] = useState([
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
  ]);

  const handleMakeParams = (key: any, value: any) => {
    if (value) {
      if (searchParams.has(key)) searchParams.set(key, value);
      else searchParams.append(key, value);
    } else searchParams.delete(key);
    setSearchParams(searchParams);
  };
  const urlMaker = () => {
    let url = "?";
    for (let key of searchParams.keys()) {
      let value = searchParams.get(key);
      url = url + `${url.length < 2 ? "" : "&"}${key}=${value}`;
    }
    return url.length > 2 ? `${url}&status=rejected` : "?status=rejected";
  };
  const setPage = (val: any) => {
    setCurrent(val);
    handleMakeParams("page", val);
    GetAdverts();
    window.scrollTo(0, 0);
  };

  // Get adverts list
  const GetAdverts = async () => {
    try {
      const { data } = await GetAdvertsListConfig(urlMaker());
      setAdverts(data.results);
      setTotal(data.count);
    } catch (error) {
      CatchError(error);
    }
  };

  useEffect(() => {
    AOS.init({ duration: 1000, once: true });
    AOS.refresh();
    GetAdverts();
  }, []);

  return (
    <div>
      <h1 style={{ marginBottom: 16 }}>
        Rad etilgan e'lonlar - {total} ta mavjud
      </h1>
      <div className="home__adverts-container flex">
        {adverts.map((item, index) => (
          <div data-aos="fade-up" data-aos-delay={50 * index} key={index}>
            <Card data={item} link="home/rejected" />
          </div>
        ))}
      </div>

      {/* Pagination */}
      <div
        style={{
          marginTop: 24,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Pagination
          total={total}
          pageSize={12}
          current={+current}
          onChange={setPage}
          showSizeChanger={false}
        />
      </div>
    </div>
  );
}

export default Rejected;
